'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import {
  DialerDashboardStats,
  CampaignStats,
  AgentStats,
  DialerCampaign
} from '@/lib/power-dialer/types'

// ==========================================
// POWER DIALER ANALYTICS PAGE
// ==========================================

export default function DialerAnalyticsPage() {
  const [dashboardStats, setDashboardStats] = useState<DialerDashboardStats | null>(null)
  const [campaigns, setCampaigns] = useState<DialerCampaign[]>([])
  const [agentStats, setAgentStats] = useState<AgentStats[]>([])
  const [callBreakdown, setCallBreakdown] = useState<{
    byOutcome: Record<string, number>
    byDisposition: Record<string, { count: number; name: string }>
    total: number
  } | null>(null)
  const [loading, setLoading] = useState(true)
  const [dateRange, setDateRange] = useState('today')
  const [selectedCampaignId, setSelectedCampaignId] = useState<string | null>(null)

  // TODO: Get from auth context
  const subAccountId = 'b0000000-0000-0000-0000-000000000001'

  useEffect(() => {
    fetchAnalytics()
  }, [dateRange, selectedCampaignId])

  const fetchAnalytics = async () => {
    setLoading(true)
    try {
      const dateParams = getDateParams(dateRange)

      // Fetch dashboard stats
      const statsRes = await fetch(`/api/v1/dialer/stats?subAccountId=${subAccountId}&type=dashboard`)
      const statsData = await statsRes.json()
      if (statsData.success) {
        setDashboardStats(statsData.data)
      }

      // Fetch campaigns
      const campaignsRes = await fetch(`/api/v1/dialer/campaigns?subAccountId=${subAccountId}`)
      const campaignsData = await campaignsRes.json()
      if (campaignsData.success) {
        setCampaigns(campaignsData.data)
      }

      // Fetch agent stats
      const agentUrl = `/api/v1/dialer/stats?subAccountId=${subAccountId}&type=agent${dateParams}`
      const agentRes = await fetch(agentUrl)
      const agentData = await agentRes.json()
      if (agentData.success) {
        setAgentStats(agentData.data)
      }

      // Fetch call breakdown
      const breakdownUrl = `/api/v1/dialer/stats?subAccountId=${subAccountId}&type=calls${dateParams}${selectedCampaignId ? `&campaignId=${selectedCampaignId}` : ''}`
      const breakdownRes = await fetch(breakdownUrl)
      const breakdownData = await breakdownRes.json()
      if (breakdownData.success) {
        setCallBreakdown(breakdownData.data)
      }

    } catch (err) {
      console.error('Error fetching analytics:', err)
    } finally {
      setLoading(false)
    }
  }

  const getDateParams = (range: string) => {
    const now = new Date()
    let dateFrom = ''
    
    switch (range) {
      case 'today':
        dateFrom = new Date(now.setHours(0, 0, 0, 0)).toISOString()
        break
      case 'yesterday':
        const yesterday = new Date(now)
        yesterday.setDate(yesterday.getDate() - 1)
        yesterday.setHours(0, 0, 0, 0)
        dateFrom = yesterday.toISOString()
        break
      case 'week':
        const weekAgo = new Date(now)
        weekAgo.setDate(weekAgo.getDate() - 7)
        dateFrom = weekAgo.toISOString()
        break
      case 'month':
        const monthAgo = new Date(now)
        monthAgo.setMonth(monthAgo.getMonth() - 1)
        dateFrom = monthAgo.toISOString()
        break
      default:
        return ''
    }
    
    return `&dateFrom=${dateFrom}&dateTo=${new Date().toISOString()}`
  }

  const formatDuration = (seconds: number) => {
    const hours = Math.floor(seconds / 3600)
    const mins = Math.floor((seconds % 3600) / 60)
    const secs = seconds % 60
    if (hours > 0) return `${hours}h ${mins}m`
    if (mins > 0) return `${mins}m ${secs}s`
    return `${secs}s`
  }

  return (
    <div className="p-6 max-w-7xl mx-auto">
      {/* Header */}
      <div className="flex justify-between items-center mb-6">
        <div>
          <div className="flex items-center gap-3">
            <Link href="/sub-account/power-dialer" className="text-gray-400 hover:text-gray-600">
              <i className="fas fa-arrow-left"></i>
            </Link>
            <h1 className="text-2xl font-bold text-gray-900 flex items-center gap-2">
              <i className="fas fa-chart-bar text-purple-600"></i>
              Dialer Analytics
            </h1>
          </div>
          <p className="text-gray-500 text-sm mt-1 ml-8">
            Track performance metrics and call statistics
          </p>
        </div>
        
        {/* Filters */}
        <div className="flex gap-3">
          <select
            value={selectedCampaignId || ''}
            onChange={(e) => setSelectedCampaignId(e.target.value || null)}
            className="px-4 py-2 border rounded-lg bg-white"
          >
            <option value="">All Campaigns</option>
            {campaigns.map(c => (
              <option key={c.id} value={c.id}>{c.name}</option>
            ))}
          </select>
          <select
            value={dateRange}
            onChange={(e) => setDateRange(e.target.value)}
            className="px-4 py-2 border rounded-lg bg-white"
          >
            <option value="today">Today</option>
            <option value="yesterday">Yesterday</option>
            <option value="week">Last 7 Days</option>
            <option value="month">Last 30 Days</option>
          </select>
        </div>
      </div>

      {loading ? (
        <div className="flex justify-center items-center h-64">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600"></div>
        </div>
      ) : (
        <div className="space-y-6">
          {/* Overview Stats */}
          <div className="grid grid-cols-2 md:grid-cols-4 lg:grid-cols-6 gap-4">
            <StatCard
              label="Total Calls"
              value={dashboardStats?.today.totalCalls || 0}
              icon="fas fa-phone"
              color="blue"
            />
            <StatCard
              label="Answered"
              value={dashboardStats?.today.answeredCalls || 0}
              icon="fas fa-check-circle"
              color="green"
            />
            <StatCard
              label="Connect Rate"
              value={`${dashboardStats?.today.connectRate || 0}%`}
              icon="fas fa-percentage"
              color="purple"
            />
            <StatCard
              label="Voicemails"
              value={dashboardStats?.today.voicemails || 0}
              icon="fas fa-voicemail"
              color="orange"
            />
            <StatCard
              label="Avg Duration"
              value={formatDuration(dashboardStats?.today.avgCallDuration || 0)}
              icon="fas fa-clock"
              color="teal"
            />
            <StatCard
              label="Total Talk Time"
              value={formatDuration(dashboardStats?.today.totalTalkTime || 0)}
              icon="fas fa-comments"
              color="indigo"
            />
          </div>

          {/* Charts Row */}
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Call Outcome Breakdown */}
            <div className="bg-white rounded-xl shadow-sm border p-6">
              <h3 className="font-semibold text-gray-900 mb-4">Call Outcomes</h3>
              {callBreakdown && callBreakdown.total > 0 ? (
                <div className="space-y-4">
                  {Object.entries(callBreakdown.byOutcome).map(([outcome, count]) => {
                    const percentage = (count / callBreakdown.total) * 100
                    return (
                      <div key={outcome}>
                        <div className="flex justify-between text-sm mb-1">
                          <span className="text-gray-600 capitalize">{outcome.replace('_', ' ')}</span>
                          <span className="font-medium">{count} ({percentage.toFixed(1)}%)</span>
                        </div>
                        <div className="w-full bg-gray-100 rounded-full h-3">
                          <div
                            className={`rounded-full h-3 ${getOutcomeColor(outcome)}`}
                            style={{ width: `${percentage}%` }}
                          ></div>
                        </div>
                      </div>
                    )
                  })}
                </div>
              ) : (
                <div className="text-center py-8 text-gray-400">
                  <i className="fas fa-chart-pie text-4xl mb-3 opacity-50"></i>
                  <p>No call data for this period</p>
                </div>
              )}
            </div>

            {/* Disposition Breakdown */}
            <div className="bg-white rounded-xl shadow-sm border p-6">
              <h3 className="font-semibold text-gray-900 mb-4">Dispositions</h3>
              {callBreakdown && Object.keys(callBreakdown.byDisposition).length > 0 ? (
                <div className="space-y-3">
                  {Object.entries(callBreakdown.byDisposition).map(([code, data]) => (
                    <div key={code} className="flex items-center justify-between p-3 bg-gray-50 rounded-lg">
                      <div className="flex items-center gap-3">
                        <span className="text-sm font-mono bg-gray-200 px-2 py-1 rounded">{code}</span>
                        <span className="text-gray-700">{data.name}</span>
                      </div>
                      <span className="font-semibold text-gray-900">{data.count}</span>
                    </div>
                  ))}
                </div>
              ) : (
                <div className="text-center py-8 text-gray-400">
                  <i className="fas fa-tags text-4xl mb-3 opacity-50"></i>
                  <p>No disposition data for this period</p>
                </div>
              )}
            </div>
          </div>

          {/* Agent Performance */}
          <div className="bg-white rounded-xl shadow-sm border p-6">
            <h3 className="font-semibold text-gray-900 mb-4">Agent Performance</h3>
            {agentStats.length > 0 ? (
              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead>
                    <tr className="border-b">
                      <th className="text-left py-3 px-4 text-sm font-medium text-gray-500">Agent</th>
                      <th className="text-center py-3 px-4 text-sm font-medium text-gray-500">Total Calls</th>
                      <th className="text-center py-3 px-4 text-sm font-medium text-gray-500">Answered</th>
                      <th className="text-center py-3 px-4 text-sm font-medium text-gray-500">Connect Rate</th>
                      <th className="text-center py-3 px-4 text-sm font-medium text-gray-500">Avg Duration</th>
                      <th className="text-center py-3 px-4 text-sm font-medium text-gray-500">Total Talk Time</th>
                    </tr>
                  </thead>
                  <tbody className="divide-y">
                    {agentStats.map(agent => (
                      <tr key={agent.agentId} className="hover:bg-gray-50">
                        <td className="py-3 px-4">
                          <div className="flex items-center gap-3">
                            <div className="w-8 h-8 bg-gradient-to-br from-blue-500 to-purple-600 rounded-full flex items-center justify-center text-white text-sm font-medium">
                              {agent.agentName.charAt(0)}
                            </div>
                            <span className="font-medium text-gray-900">{agent.agentName}</span>
                          </div>
                        </td>
                        <td className="py-3 px-4 text-center">{agent.totalCalls}</td>
                        <td className="py-3 px-4 text-center text-green-600 font-medium">{agent.answeredCalls}</td>
                        <td className="py-3 px-4 text-center">
                          <span className={`px-2 py-1 rounded text-sm ${
                            agent.totalCalls > 0
                              ? getConnectRateClass((agent.answeredCalls / agent.totalCalls) * 100)
                              : 'bg-gray-100 text-gray-500'
                          }`}>
                            {agent.totalCalls > 0
                              ? `${Math.round((agent.answeredCalls / agent.totalCalls) * 100)}%`
                              : 'N/A'}
                          </span>
                        </td>
                        <td className="py-3 px-4 text-center">{formatDuration(agent.avgCallDuration)}</td>
                        <td className="py-3 px-4 text-center font-medium">{formatDuration(agent.totalTalkTime)}</td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            ) : (
              <div className="text-center py-8 text-gray-400">
                <i className="fas fa-users text-4xl mb-3 opacity-50"></i>
                <p>No agent data for this period</p>
              </div>
            )}
          </div>

          {/* Campaign Performance */}
          <div className="bg-white rounded-xl shadow-sm border p-6">
            <h3 className="font-semibold text-gray-900 mb-4">Campaign Performance</h3>
            {campaigns.length > 0 ? (
              <div className="overflow-x-auto">
                <table className="w-full">
                  <thead>
                    <tr className="border-b">
                      <th className="text-left py-3 px-4 text-sm font-medium text-gray-500">Campaign</th>
                      <th className="text-center py-3 px-4 text-sm font-medium text-gray-500">Status</th>
                      <th className="text-center py-3 px-4 text-sm font-medium text-gray-500">Progress</th>
                      <th className="text-center py-3 px-4 text-sm font-medium text-gray-500">Total Calls</th>
                      <th className="text-center py-3 px-4 text-sm font-medium text-gray-500">Answered</th>
                      <th className="text-center py-3 px-4 text-sm font-medium text-gray-500">Voicemails</th>
                      <th className="text-center py-3 px-4 text-sm font-medium text-gray-500">Connect Rate</th>
                    </tr>
                  </thead>
                  <tbody className="divide-y">
                    {campaigns.map(campaign => (
                      <tr key={campaign.id} className="hover:bg-gray-50">
                        <td className="py-3 px-4">
                          <Link 
                            href={`/sub-account/power-dialer?campaign=${campaign.id}`}
                            className="font-medium text-gray-900 hover:text-blue-600"
                          >
                            {campaign.name}
                          </Link>
                          <p className="text-xs text-gray-500">{campaign.type}</p>
                        </td>
                        <td className="py-3 px-4 text-center">
                          <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                            campaign.status === 'active' ? 'bg-green-100 text-green-700' :
                            campaign.status === 'paused' ? 'bg-yellow-100 text-yellow-700' :
                            campaign.status === 'completed' ? 'bg-blue-100 text-blue-700' :
                            'bg-gray-100 text-gray-600'
                          }`}>
                            {campaign.status}
                          </span>
                        </td>
                        <td className="py-3 px-4">
                          <div className="flex items-center gap-2">
                            <div className="flex-1 bg-gray-200 rounded-full h-2 min-w-[60px]">
                              <div
                                className="bg-blue-600 rounded-full h-2"
                                style={{
                                  width: `${campaign.totalContacts > 0
                                    ? (campaign.contactsDialed / campaign.totalContacts) * 100
                                    : 0}%`
                                }}
                              ></div>
                            </div>
                            <span className="text-xs text-gray-500 whitespace-nowrap">
                              {campaign.contactsDialed}/{campaign.totalContacts}
                            </span>
                          </div>
                        </td>
                        <td className="py-3 px-4 text-center">{campaign.totalCalls}</td>
                        <td className="py-3 px-4 text-center text-green-600 font-medium">{campaign.answeredCalls}</td>
                        <td className="py-3 px-4 text-center">{campaign.voicemails}</td>
                        <td className="py-3 px-4 text-center">
                          <span className={`px-2 py-1 rounded text-sm ${
                            campaign.totalCalls > 0
                              ? getConnectRateClass((campaign.answeredCalls / campaign.totalCalls) * 100)
                              : 'bg-gray-100 text-gray-500'
                          }`}>
                            {campaign.totalCalls > 0
                              ? `${Math.round((campaign.answeredCalls / campaign.totalCalls) * 100)}%`
                              : 'N/A'}
                          </span>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            ) : (
              <div className="text-center py-8 text-gray-400">
                <i className="fas fa-bullhorn text-4xl mb-3 opacity-50"></i>
                <p>No campaigns found</p>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  )
}

// ==========================================
// HELPER COMPONENTS
// ==========================================

function StatCard({
  label,
  value,
  icon,
  color
}: {
  label: string
  value: string | number
  icon: string
  color: string
}) {
  const colorClasses: Record<string, string> = {
    blue: 'bg-blue-100 text-blue-600',
    green: 'bg-green-100 text-green-600',
    purple: 'bg-purple-100 text-purple-600',
    orange: 'bg-orange-100 text-orange-600',
    teal: 'bg-teal-100 text-teal-600',
    indigo: 'bg-indigo-100 text-indigo-600',
    red: 'bg-red-100 text-red-600'
  }

  return (
    <div className="bg-white rounded-xl shadow-sm border p-4">
      <div className={`w-10 h-10 rounded-lg ${colorClasses[color]} flex items-center justify-center mb-3`}>
        <i className={icon}></i>
      </div>
      <p className="text-2xl font-bold text-gray-900">{value}</p>
      <p className="text-sm text-gray-500">{label}</p>
    </div>
  )
}

// ==========================================
// HELPER FUNCTIONS
// ==========================================

function getOutcomeColor(outcome: string) {
  const colors: Record<string, string> = {
    connected: 'bg-green-500',
    answered: 'bg-green-500',
    voicemail: 'bg-blue-500',
    no_answer: 'bg-yellow-500',
    busy: 'bg-orange-500',
    failed: 'bg-red-500',
    unknown: 'bg-gray-400'
  }
  return colors[outcome] || colors.unknown
}

function getConnectRateClass(rate: number) {
  if (rate >= 50) return 'bg-green-100 text-green-700'
  if (rate >= 30) return 'bg-yellow-100 text-yellow-700'
  if (rate >= 10) return 'bg-orange-100 text-orange-700'
  return 'bg-red-100 text-red-700'
}
